<template>
	<view class="newsList" @click="enterNews(Newsid)">
		<u-image v-if="imgUrl" width="40%" height="168rpx" :src="imgUrl" mode="aspectFit">
			<u-loading slot="loading"></u-loading>
			<view slot="error" style="display: flex;justify-content: center;align-items: center;">
				<text>error</text>
			</view>
		</u-image>
		<image lazy-load class="image" v-else :src="defaultImg" mode=""></image>
		<view class="news_content_item">
			<view class="texttile">
				<text>{{title||'无标题'}}</text>
			</view>
			<text class="textcontent">{{content}}</text>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				defaultImg:uni.getStorageSync('uploadLogo'),
			}
		},
		props:{
			Newsid:{
				type:Number,
				default:null
			},
			imgUrl:{
				type:String,
				default:""
			},
			title:{
				type:String,
				default:""
			},
			content:{
				type:String,
				default:""
			},
			
		},
		methods:{
			enterNews(id){
				uni.navigateTo({
					url:'/pages/news/newsDetail?id='+id,
					animationType:'pop-in'
				})
			}
		}
	}
</script>

<style>
	.newsList{
		padding: 20rpx 30rpx;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		margin-top: 32rpx;
		background-color: #FFFFFF;
	}
	/* #ifdef MP-WEIXIN */
	.newsList image{
		width: 126px!important;
		height: 168rpx;
		border-radius: 20rpx;
	}
	/* #endif */
	.newsList .image{
		width: 40%;
		height: 168rpx;
		border-radius: 20rpx;
	}
	.news_content_item{
		width: 60%;
		padding-left: 28rpx;
		color: #000000;
		box-sizing: border-box;
	}
	.news_content_item .texttile{
		font-size: 30rpx;
		font-weight: 600;
		margin-bottom: 12.5rpx;
		font-family: PingFang-SC-Bold;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap; 
	}
	.news_content_item .textcontent{
		font-size: 24rpx;
		font-family: PingFang-SC-Bold;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
		overflow: hidden;
	}
</style>
